.numbers {
  &__title {
    max-width: 277px;
    @include screen(laptop) {
      max-width: auto;
    }
  }
  &__heading {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 29px 0 25px;
    gap: 30px;
    @include screen(laptop) {
      gap: 17px;
      padding: 78px 0 43px;
    }
  }
  &__subtitle {
    font-weight: 500;
    text-align: center;
    color: $black;
    font-size: 14px;
    line-height: 20px;
    @include screen(laptop) {
      font-size: 26px;
      line-height: 26px;
    }
  }
  &__items {
    display: grid;
    border-top: 1px solid $gray-400;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      border-bottom: 1px solid $gray-400;
    }
  }
  &__container {
    @include screen(laptop) {
      margin: 0 auto;
    }
  }
  &__item {
    padding: 26px 0;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    position: relative;
    border-bottom: 1px solid $gray-400;
    @include screen(laptop) {
      border-bottom: none;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        height: 109px;
        background-color: $gray-400;
        width: 1px;
      }
    }
    @include screen(laptop) {
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
    span {
      margin-bottom: 12px;
      min-height: 19px;
      font-size: 16px;
      font-weight: 700;
      line-height: 20px;
      color: $blue-300;
      text-transform: uppercase;
    }
    p {
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      color: $blue-300;
      max-width: 133px;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 22px;
        max-width: 100%;
      }
    }
  }
  &__number {
    display: flex;
    align-items: center;
    gap: 38px;
    @include screen(laptop) {
      display: block;
    }
  }
  &__value {
    font-size: 39px;
    line-height: 39px;
    font-weight: 300;
    @include gradient-text-blue-to-pink;
    @include screen(laptop) {
      margin-bottom: 16px;
      font-size: 50px;
      line-height: 50px;
    }
  }
}