.info {
  &__wrapper {
    border-bottom: 1px solid $gray-400;
    padding: 31px 0;
    @include screen(laptop) {
      padding: 62px 0;
    }
  }
  &__items {
    display: grid;
    padding: 24px 22px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    border-radius: 50px;
    background-color: $gray-300;
    gap: 34px;
    @include screen(laptop) {
      gap: 0;
      padding: 34px 53px 34px 20px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
    }
    &:nth-child(2) {
      flex-direction: column-reverse;
      position: relative;
      padding-bottom: 32px;
      @include screen(laptop) {
        flex-direction: row;
        padding-left: 197px;
      }
      .info__image {
        @include screen(laptop) {
          min-width: 197px;
          position: absolute;
          left: 0;
          top: -44px;
        }
      }
    }
  }
  &__image {
    min-width: 90px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 11px;
    @include screen(laptop) {
      align-items: flex-start;
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    font-weight: 500;
    text-transform: uppercase;
    @include screen(laptop) {
      font-size: 20px;
      line-height: 25px;
      text-align: left;
      font-weight: 300;
      color: $gray-500;
    }
  }
  &__text {
    font-size: 12px;
    line-height: 20px;
    font-weight: 300;
    text-align: center;
    color: $gray-500;
    @include screen(laptop) {
      font-size: 16px;
      text-align: left;
      line-height: 20px;
    }
  }
  &__link {
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 12px;
    margin-left: auto;
    font-size: 12px;
    line-height: 15px;
  }
}