.reviews {
  &__title {
    max-width: 213px;
    margin: 0 auto;
    @include screen(laptop) {
      max-width: 100%;
    }
  }
  &__wrapper {
    padding: 31px 0 70px;
    position: relative;
    @include screen(laptop) {
      padding: 50px 0 78px;
    }
  }
  &__slider {
    position: relative;
    &-main {
      z-index: 20;
      padding: 34px 0 0;
      @include screen(laptop) {
        padding: 57px 0 0;
      }
    }
    &-thumbs {
      .swiper {
        padding: 44px 0;
        &-slide {
          pointer-events: none;
          opacity: 0.5;
          text-align: center;
          p {
            text-transform: uppercase;
            font-size: 12px;
            display: inline-block;
            line-height: 16px;
            border-bottom: 1px solid $black;
          }
          &-active {
            opacity: 1;
          }
        }
      }
    }
    .swiper {
      &-navigation {
        z-index: 20;
        pointer-events: none;
        position: absolute;
        width: 100%;
        display: flex;
        top: 156px;
        transform: translateY(50%);
        justify-content: space-between;
        @include screen(laptop) {
          transform: translateY(0);
          top: var(--swiper-navigation-top-offset, 50%);
        }
      }
      &-button {
        &-prev,
        &-next {
          pointer-events: all;
          position: relative;
          display: flex;
          align-items: center;
          transition: $transition-default;
          justify-content: center;
          margin: 0;
          background-color: $white;
          border-radius: 50%;
          box-shadow: 3px 2px 12px 0px rgba($black, 0.2);
          right: auto;
          left: auto;
          width: 24px;
          height: 24px;
          @include screen(laptop) {
            width: 63px;
            height: 63px;
          }
          svg {
            min-width: 4px;
            max-width: 4px;
            @include screen(laptop) {
              min-width: 9px;
              max-width: 9px;
            }
          }
          &:before,
          &:after {
            display: none;
          }
        }
        &-prev {
          left: -12px;
          @include screen(laptop) {
            left: 0;
          }
        }
        &-next {
          right: -12px;
          @include screen(laptop) {
            right: 0;
          }
        }
      }
      &-pagination {
        height: 17px;
        position: relative;
        bottom: auto;
        top: auto;
        left: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;
        &-bullet {
          width: 7px;
          height: 7px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          opacity: 1;
          background-color: $blue-300;
          &:before {
            width: 17px;
            position: absolute;
            transition: border-color $transition-default;
            content: '';
            height: 17px;
            border: 1px solid transparent;
            border-radius: 50%;
          }
          &-active {
            &:before {
              border-color: $blue-300;
            }
          }
        }
      }
    }
  }
  &__slide {
    display: flex;
    flex-direction: column;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      border-top: 1px solid $gray-400;
      border-bottom: 1px solid $gray-400;
      padding: 0 85px;
    }
  }
  &__video {
    iframe {
      width: 100%;
      min-height: 220px;
      @include screen(laptop) {
        min-height: 406px;
      }
    }
  }
  &__content {
    padding: 33px 0 0;
    @include screen(laptop) {
      padding: 62px 0 62px 87px;
    }
  }
  &__author {
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
    @include screen(laptop) {
      text-align: left;
      font-size: 26px;
      line-height: 26px;
    }
  }
  &__city {
    margin-top: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-transform: uppercase;
    text-align: center;
    color: $gray-200;
    @include screen(laptop) {
      text-align: left;
      font-size: 18px;
    }
  }
  &__text {
    text-align: center;
    font-weight: 400;
    max-width: 226px;
    font-size: 14px;
    line-height: 20px;
    margin: 29px auto 0;
    @include screen(laptop) {
      text-align: left;
      margin: 36px 0 0;
      font-size: 18px;
      line-height: 26px;
      max-width: 408px;
    }
    br {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
}