.scroll-head {
  overflow: hidden;
  position: relative;
  padding: 84px 0 47px;
  &__grid {
    position: absolute;
    padding: 104px 0 47px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__container {
    grid-column: span 1 / span 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 58px
  }
  @include screen(laptop) {
    padding: 104px 0 31px;
  }
  &__image {
    position: absolute;
    right: -300px;
    svg {
      height: 1000px;
      @include screen(laptop) {
        height: 100%;
      }
    }
    @include screen(laptop) {
      right: 0;
    }
    svg {
      .reveal-head {
        opacity: 0.3;
        transition: opacity $transition-default;
        &.active {
          opacity: 1;
        }
      }
    }
  }
  &__line {
    min-height: 460px;
    width: 100%;
    top: 0;
    position: relative;
    z-index: 20;
    svg {
      overflow: visible;
    }
    &-1 {
      padding-right: 339px;
    }
    &-2 {
      padding-right: 296px;
    }
    &-3 {
      padding-right: 470px;
    }
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__heading {
    padding-bottom: 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__sliders {
    display: flex;
    gap: 40px;
    flex-direction: column;
    grid-column: span 8 / span 8;
    @include screen(laptop) {
      gap: 58px;
      grid-column: span 1 / span 1;
    }
  }
  &__tip {
    position: absolute;
    right: 0;
    transform: translateX(100%) translateY(-50%);
    top: 50%;
    display: none;
    font-size: 10px;
    font-weight: 300;
    line-height: 12px;
    @include screen(laptop) {
      display: block;
    }
  }
  &__slider {
    box-shadow: 5px 4px 20px 0px rgba($black, 0.2);
    border-radius: 10px;
    position: relative;
    padding: 20px;
    background-color: $white;
    @include screen(laptop) {
      padding: 22px 94px 40px;
    }
    &-0 {
      .scroll-head__tip {
        transform: translateX(200px) translateY(20px);
      }
    }
    &-1 {
      .scroll-head__tip {
        transform: translateX(200px) translateY(-60px);
      }
    }
    &-2 {
      .scroll-head__tip {
        transform: translateX(160px) translateY(-50%);
      }
    }
    .swiper {
      width: 100%;
      &-navigation {
        z-index: 20;
        pointer-events: none;
        position: absolute;
        left: 0;
        width: 100%;
        display: flex;
        top: calc(50% + 10px); 
        justify-content: space-between;
      }
      &-button {
        &-prev,
        &-next {
          pointer-events: all;
          position: relative;
          display: flex;
          align-items: center;
          transition: $transition-default;
          justify-content: center;
          margin: 0;
          background-color: $white;
          border-radius: 50%;
          box-shadow: 3px 2px 12px 0px rgba($black, 0.2);
          right: auto;
          left: auto;
          width: 24px;
          height: 24px;
          @include screen(laptop) {
            width: 42px;
            height: 42px;
          }
          svg {
            min-width: 4px;
            max-width: 4px;
            @include screen(laptop) {
              min-width: 9px;
              max-width: 9px;
            }
          }
          &:before,
          &:after {
            display: none;
          }
        }
        &-prev {
          left: -12px;
          @include screen(laptop) {
            left: 24px;
          }
        }
        &-next {
          right: -12px;
          @include screen(laptop) {
            right: 24px;
          }
        }
      }
    }
  }
  &__subtitle {
    font-weight: 400;
    text-transform: uppercase;
    font-size: 9px;
    line-height: 11px;
    margin-bottom: 4px;
    @include screen(laptop) {
      font-size: 14px;
      margin-bottom: 10px;
      line-height: 17px;
    }
  }
  &__preview {
    img {
      width: 100%;
      height: 100%;
      min-height: 100px;
      object-fit: cover;
      @include screen(laptop) {
        min-height: 219px;
      }
    }
  }
  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2px;
    padding: 3px;
    background: linear-gradient(90deg, rgba(249, 207, 211, 0.43) 0%, rgba(85, 122, 188, 0.43) 100%);
    @include screen(laptop) {
      padding: 8px;
    }
    span {
      font-size: 10px;
      font-weight: 400;
      line-height: 12px;
      text-transform: uppercase;
      color: $black;
    }
  }
  &__slide {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 20px;
    @include screen(laptop) {
      gap: 30px;
    }
  }
  &__title {
    font-weight: 700;
    text-transform: uppercase;
    color: $black;
    font-size: 14px;
    line-height: 17px;
    padding-bottom: 6px;
    border-bottom: 1px solid $gray-400;
    display: inline-block;
    @include screen(laptop) {
      font-size: 21px;
      margin-bottom: 8px;
      line-height: 26px;
    }
  }
  &__text {
    text-transform: uppercase;
    color: $gray-200;
    font-weight: 400;
    font-size: 8px;
    margin-top: 6px;
    width: 100%;
    line-height: 10px;
    @include screen(laptop) {
      font-size: 12px;
      display: inline-block;
      line-height: 18px;
      margin-top: 0;
      width: auto;
    }
    &:last-child {
      padding-bottom: 6px;
      border-bottom: 1px solid $gray-400;
      @include screen(laptop) {
        padding-bottom: 0;
      }
    }
    strong {
      font-weight: 500;
    }
  }
}