@mixin screen($breakpoint) {
  @if $breakpoint == desktop {
    @media only screen and (min-width: $screen-desktop) {
      @content;
    }
  }

  @if $breakpoint == laptop {
    @media only screen and (min-width: $screen-laptop) {
      @content;
    }
  }

  @if $breakpoint == tablet-2x {
    @media only screen and (min-width: $screen-tablet-2x) {
      @content;
    }
  }

  @if $breakpoint == tablet {
    @media only screen and (min-width: $screen-tablet) {
      @content;
    }
  }

  @if $breakpoint == mobile {
    @media only screen and (min-width: $screen-mobile) {
      @content;
    }
  }
}

@mixin gradient-text-blue-to-pink {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  background-image: $gradient-blue-to-pink;
}


@mixin gradient-text-blue-to-pink-2x {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  background-image: $gradient-blue-to-pink-2x;
}